'use client'

// app/markdown-page/page.js
import fs from 'fs'
import path from 'path'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm' // 只用这个
import 'highlight.js/styles/github.css' // 只用这个
import { useState } from 'react'
import { ChevronLeft, ChevronRight } from 'lucide-react'

const images = ["/solar/1.jpg", "/solar/2.jpg", "/solar/3.jpg", "/solar/4.jpg", "/solar/5.jpg"]

export default async function SolarBatterySpecs() {
  const mdFilePath = path.join(process.cwd(), 'public', 'solar-battery-specs.md')
  const mdContent = await fs.promises.readFile(mdFilePath, 'utf8')
  
  // 新增：轮播图当前索引
  const [currentIndex, setCurrentIndex] = useState(0)

  return (
    // 你原来的 return 内容
    <div className="max-w-5xl mx-auto px-6 py-12 space-y-12">

    {/* ==================== 轮播图开始 ==================== */}
    <div className="relative w-full max-w-4xl mx-auto overflow-hidden rounded-2xl shadow-2xl">
      {/* 图片列表（请按需修改下面的路径，放在 public 文件夹里即可直接访问 */}
      <div className="flex animate-carousel">
        <img src="/280w-german-solar-panel.jpg" alt="太阳能电池 1" className="w-full flex-none object-cover aspect-video" />
        <img src="/solar/2.jpg" alt="太阳能电池 2" className="w-full flex-none object-cover aspect-video" />
        <img src="/solar/3.jpg" alt="太阳能电池 3" className="w-full flex-none object-cover aspect-video" />
        <img src="/solar/4.jpg" alt="太阳能电池 4" className="w-full flex-none object-cover aspect-video" />
        <img src="/solar/5.jpg" alt="太阳能电池 5" className="w-full flex-none object-cover aspect-video" />
        {/* 为了无缝循环，再补一张第一张 */}
        <img src="/solar/1.jpg" alt="太阳能电池 1" className="w-full flex-none object-cover aspect-video" />
      </div>

      {/* 可选：小圆点指示器（纯装饰） */}
      <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex gap-2">
        {[0, 1, 2, 3, 4].map((i) => (
          <div key={i} className="w-2 h-2 bg-white/70 rounded-full" />
        ))}
      </div>
    </div>
    {/* ==================== 轮播图结束 ==================== */}

    {/* 你原来的 Markdown 内容，完全不动 */}
    <div className="max-w-3xl mx-auto">
      <ReactMarkdown remarkPlugins={[remarkGfm]}>
        {mdContent}
      </ReactMarkdown>
    </div>
  </div>
)
}
  